.main {
  background-color: #0c494b;
  font-family: "Oxygen", sans-serif;
  width: 100%;
  height: auto;
  min-height: 100vh; }
  .main__input {
    padding: 10px;
    border-radius: 10px;
    margin-left: 20px;
    margin-top: 10px; }
  .main__button {
    padding: 10px;
    border-radius: 10px;
    margin-left: 15px; }
    .main__button:hover {
      background-color: rgba(243, 12, 12, 0.815);
      color: white; }

.sections {
  display: flex;
  flex-direction: row;
  margin: 15px 9px 0 9px;
  margin-top: 15px;
  height: auto; }
  .sections__favoritesSection {
    width: 20%;
    margin-right: 20px;
    margin-left: 100px;
    color: white; }
  .sections__searchSection {
    margin-left: 100px;
    width: 65%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center; }
  .sections__input--reset {
    padding: 10px;
    border-radius: 10px;
    width: 100px;
    align-content: center; }
    .sections__input--reset:hover {
      background-color: rgba(243, 12, 12, 0.815);
      color: white; }

.clickSeries {
  border: 10px solid rgba(243, 12, 12, 0.815);
  color: white;
  font-size: larger; }

.main__li--listData {
  list-style: none;
  display: inline-block;
  color: white; }

.main__li--favListData {
  list-style: none; }

@keyframes translate {
  from {
    transform: translateY 0; }
  50% {
    transform: translateY -40px; }
  to {
    transform: translateY 0; } }

.header {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #153c3d;
  font-family: "Acme", sans-serif; }
  .header__name {
    color: rgba(243, 12, 12, 0.815); }
  .header__image {
    height: 60px;
    animation: translate 4s infinite; }
  .header__title {
    color: white; }

.footer {
  width: 100%;
  height: 60px;
  background-color: #153c3d;
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .footer__copy {
    color: white;
    margin-left: 40px; }
  .footer__name {
    color: rgba(243, 12, 12, 0.815);
    margin-right: 40px;
    font-family: "Acme", sans-serif;
    font-size: larger; }

.page {
  height: 100vh; }
